import React from 'react';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import Item from './item';

const useStyles = makeStyles((theme) => ({
   
   paper: {
      padding: theme.spacing(2),
      textAlign: 'center',
      color: theme.palette.text.secondary,
    },

   subtitle: {
      margin: 0,
      textAlign:'left',
      fontWeight: 300,
      fontSize: 18,
   },

 }));

function Weapons() {
   
   const classes = useStyles();

   const weaponsList = [
      { name: 'Llave Ingresa', color: '#888' },
      { name: 'Candelabro', color: '#888' },
      { name: 'Data', color: '#888' },
      { name: 'Pistola', color: '#888' },
      { name: 'Tubería', color: '#888' },
      { name: 'Soga', color: '#888' },
   ]
   
   return (
      <Grid key="weapons-grid" item xs={12} sm={4}>
         <Paper key="weapons-paper" variant="outlined" square className={classes.paper}>
            <h2 key='weapons-title' className={classes.subtitle}>Armas</h2>
            <List key='weapons-list' className={classes.root}>
               { weaponsList.map((value, index) => {
                  let props = {
                     key: index, 
                     ...value
                  }
                  return (
                     <>
                        <Item icon='weapons' props={props} />
                        { ( index+1 < weaponsList.length ) ? <Divider variant="inset" component="li" /> : null } 
                     </>
                  )
                  })
               }
            </List>
         </Paper>
      </Grid>
   )
}

export default Weapons;
